#project {
  text-align: center;
  background-color: #1a134d;
  border-radius: 1.5rem;
  width: 64%; /* Adjusted width for responsiveness */
  max-width: 100%; /* Ensure it spans the full width on smaller screens */
  margin: 20px auto; /* Center and add margin */
  padding: 20px 10px 20px 20px; /* Reduced right padding */
  box-sizing: border-box; /* Include padding and border in width/height */
  height: auto; /* Make height auto for flexibility */
  min-height: 550px; /* Ensure minimum height */
}

.projects-container {
  display: flex;
  flex-direction: column; /* Stack vertically on small screens */
  justify-content: center;
  align-items: center; /* Center align */
  text-align: center;
}

.tab-buttons-column {
  display: flex;
  flex-direction: column; /* Stack buttons vertically */
  align-items: center;
  margin-bottom: 20px; /* Space between buttons and content on small screens */
  width: 100%; /* Full width for smaller screens */
}

.tab-buttons-list {
  list-style-type: none;
  padding: 0; /* Reset padding */
  margin: 0;
}

.tab-buttons-list li {
  margin-bottom: 10px; /* Spacing between buttons */
}

.TabButton {
  width: 100%; /* Full width for buttons */
  max-width: 200px; /* Max width for larger screens */
  box-sizing: border-box; /* Include padding and border in width */
}

.tab-content-container {
  width: 100%; /* Full width */
  display: flex;
  justify-content: center;
}

.tab-content {
  text-align: center;
  max-width: 800px;
  width: 100%; /* Full width */
  padding-right: 0; /* Remove right padding */
  margin-right: 0; /* Remove right margin */
}

.tab-content a {
  color: white;
  display: block;
  margin-bottom: 20px;
  padding-top: 20px;
  font-size: 1.25rem;
}

.tab-content a:hover {
  color: #c1a9ee;
}

.description {
  text-align: justify;
  margin-bottom: 20px;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem; /* Adjust font size for better responsiveness */
}

.skills_p {
  text-align: left;
}

/* Media queries for responsive design */
@media (min-width: 768px) {
  .projects-container {
    flex-direction: row; /* Side by side on larger screens */
    align-items: flex-start; /* Align items to the top */
  }

  .tab-buttons-column {
    margin-right: 20px; /* Margin between buttons and content */
    margin-bottom: 0; /* Remove bottom margin */
    width: auto; /* Auto width for larger screens */
  }

  .TabButton {
    width: 200px; /* Fixed width for larger screens */
  }
}

@media (min-width: 1024px) {
  #project {
    padding: 30px; /* Adjust padding for larger screens */
  }

  h2 {
    font-size: 2.5rem; /* Increase font size for larger screens */
  }
}

/* Media queries for phones */
@media (max-width: 767px) {
  #project {
    width: 90%; /* Adjust width for smaller screens */
    padding: 15px; /* Adjust padding for smaller screens */
    min-height: auto; /* Adjust minimum height */
  }

  h2 {
    font-size: 1.5rem; /* Adjust font size for smaller screens */
  }

  .tab-content a {
    font-size: 1rem; /* Adjust font size for smaller screens */
  }

  .description {
    text-align: center; /* Center text for smaller screens */
    font-size: 1rem; /* Adjust font size for smaller screens */
  }

  .skills_p {
    text-align: center; /* Center text for smaller screens */
  }
}
