#about {
  text-align: center; /* Centers the content horizontally */
  padding-bottom: 80px;
}

.about-content {
  display: flex;
  flex-direction: row; /* Horizontal layout for larger screens */
  justify-content: center; /* Centers items horizontally */
  flex-wrap: wrap; /* Allows wrapping on smaller screens */
}

.about-text {
  flex: 1; /* Take up remaining space */
  padding-right: 20px; /* Adjust spacing between text and image */
  font-size: 1.6875rem; /* Use rem for scalable text */
  text-align: justify;
  max-width: 600px; /* Adjust to prevent text from being too wide */
}

.about-image {
  width: 350px; /* Adjust width of the image container */
  height: 350px; /* Adjust height of the image container */
  margin-left: 20px; /* Adjust margin between text and image */
  flex-shrink: 0; /* Prevent the image from shrinking */
}

h2 {
  padding-bottom: 0;
  margin-bottom: 0;
}

.pText {
  font-size: 1.5625rem; /* Use rem for scalable text */
  text-align: justify;
  text-justify: inter-character;
}

/* Media queries for responsive design */
@media (max-width: 1024px) {
  .about-text {
    font-size: 1.375rem; /* Adjust font size */
  }

  .about-image {
    width: 300px; /* Adjust width of the image container */
    height: 300px; /* Adjust height of the image container */
  }
}

@media (max-width: 768px) {
  .about-content {
    flex-direction: column; /* Stack items vertically on smaller screens */
    align-items: center; /* Center items vertically */
  }

  .about-text {
    padding-right: 0; /* Remove padding on smaller screens */
    padding-bottom: 20px; /* Add space between text and image */
    text-align: center; /* Center text on smaller screens */
  }

  .about-image {
    width: 250px; /* Adjust width of the image container */
    height: 250px; /* Adjust height of the image container */
    margin-left: 0; /* Remove left margin on smaller screens */
  }

  .pText {
    font-size: 1.25rem; /* Adjust font size */
  }
}

@media (max-width: 480px) {
  .about-text {
    font-size: 1.125rem; /* Adjust font size */
    padding-bottom: 15px; /* Add more space between text and image */
  }

  .about-image {
    width: 200px; /* Adjust width of the image container */
    height: 200px; /* Adjust height of the image container */
  }

  .pText {
    font-size: 1rem; /* Adjust font size */
  }
}
