#skills {
  text-align: center;
  margin: 100px 0 20px 0; /* Increase top margin, decrease bottom margin */
  padding-top: 40px;
  padding-bottom: 20px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.skill-bubble {
  background-color: #1a134d;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
  align-items: center; /* Center content horizontally */
}

.skill-bubble h3 {
  margin-top: 0;
  margin-bottom: 10px;
  text-align: center;
  font-size: 25px;
  font-weight: bold;
}

.skill-bubble p {
  margin: 0;
  color: white;
  text-align: center;
  display: flex;
  justify-content: center; /* Center text horizontally */
  align-items: center; /* Center text vertically */
  height: 100%; /* Ensure full height of the bubble */
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr; /* Single column layout for small screens */
  }

  .skill-bubble {
    padding: 15px; /* Adjust padding for small screens */
  }

  .skill-bubble h3 {
    font-size: 20px; /* Adjust font size for small screens */
  }

  .skill-bubble p {
    font-size: 14px; /* Adjust font size for small screens */
  }
}

@media (max-width: 480px) {
  .skills-grid {
    gap: 10px; /* Reduce gap for very small screens */
  }

  .skill-bubble {
    padding: 10px; /* Further adjust padding for very small screens */
  }

  .skill-bubble h3 {
    font-size: 18px; /* Adjust font size for very small screens */
  }

  .skill-bubble p {
    font-size: 12px; /* Adjust font size for very small screens */
  }
}
