/* app.css */
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900&display=swap");

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400; /* Use valid font-weight value */
  font-style: normal;
  font-size: 1rem; /* Relative unit for scalable text */
  margin: 0;
  padding: 0;
  background: #4776e6;
  background: -webkit-linear-gradient(90deg, #4776e6 0%, #8e54e9 100%);
  background: linear-gradient(90deg, #4776e6 0%, #8e54e9 100%);
  color: #ffffff;
}

* {
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

main {
  width: 100%;
  max-width: 1500px;
  padding: 10px;
}

header,
main {
  width: 100%;
}

section {
  width: 100%;
  padding: 20px;
  margin: 10px 0; /* Reduce margin between sections */
}

h1 {
  font-family: "Roboto", sans-serif;
  font-weight: 800;
  font-style: normal;
  font-size: 3.75rem; /* Relative unit */
  color: #ffffff;
}

h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 3.125rem; /* Relative unit */
  font-style: normal;
  color: #ffffff;
  margin-bottom: 20px; /* Adds space below the heading */
}

h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 1.5rem; /* Relative unit */
  color: #ffffff;
}

p {
  font-family: "Roboto", sans-serif;
  font-size: 1.5625rem; /* Relative unit */
  color: #ffffff;
  max-width: 550px; /* Adjust as needed */
  margin: 0 auto; /* Centers the paragraph horizontally */
  text-align: justify;
}

/* Media queries for responsive design */
@media (max-width: 1024px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.5rem;
  }

  h3 {
    font-size: 1.25rem;
  }

  p {
    font-size: 1.25rem;
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }

  h2 {
    font-size: 2rem;
  }

  h3 {
    font-size: 1rem;
  }

  p {
    font-size: 1rem;
    max-width: 100%;
    padding: 0 10px;
  }

  section {
    padding: 10px;
    margin: 10px 0;
  }

  main {
    padding: 10px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  h3 {
    font-size: 0.875rem;
  }

  p {
    font-size: 0.875rem;
  }

  section {
    padding: 5px;
    margin: 5px 0;
  }

  main {
    padding: 5px;
  }
}
