/* Base Styles */
.container {
  display: flex;
  align-items: center;
  justify-content: space-between; /* Space between items */
  flex-wrap: nowrap; /* No wrapping */
  padding: 1%;
  max-width: 1500px; /* Adjust the maximum width of the container */
  margin: auto; /* Center the container */
}

#home {
  text-align: left;
  margin: 0;
  flex: 0 0 25%; /* Take up 15% of the container width */
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding-bottom: 90px;
  padding-left: 50px;
}

.contact-links {
  margin: 0;
  padding: 0;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

p {
  margin-top: 0;
  padding-top: 0;
}

h1 {
  margin-bottom: 0;
  padding-bottom: 18px;
  margin-top: 0;
}

img {
  width: 100%; /* Ensure image takes full width of its container */
  height: auto; /* Maintain aspect ratio */
  max-width: 100%; /* Limit maximum width to its container */
  display: block; /* Ensure image is treated as a block element */
  margin: 20px 0; /* Adjust spacing around the image */
  box-sizing: border-box;
}

.cvButton {
  background: #0f114e;
  color: white;
  border: none;
  box-shadow: none;
  font-size: 20px;
  border-radius: 1.5rem;
  box-sizing: border-box;
  cursor: pointer;
  display: block; /* Changed to block to center */
  font-family: "Roboto", sans-serif;
  line-height: 1;
  text-align: center;
  text-decoration: none #0d172a solid;
  text-decoration-thickness: auto;
  transition: all 0.1s cubic-bezier(0.4, 0, 0.2, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  padding: 1rem 2rem;
  width: 200px;
  margin: 10px auto; /* Reduce the margin to decrease horizontal space */
  border: white solid 2px;
}

.cvButton:hover {
  color: #0f114e;
  background-color: white;
  border: #0f114e solid 2px;
}

.home_p {
  font-size: 1.35rem;
}

/* Media Queries */
@media (max-width: 1024px) {
  .container {
    max-width: 100%; /* Ensure container adapts to smaller screens */
    justify-content: center; /* Center content on larger tablets */
  }

  #home {
    flex: 0 0 25%;
  }

  img {
    flex: 1 0 80%; /* Take up 80% of the container width */
    max-width: 80%; /* Limit width to 80% of the container width */
    margin: 20px 0; /* Adjust spacing for smaller screens */
  }
}

@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: center;
  }

  #home {
    padding: 10px;
    padding-top: 20px;
    flex: 0 0 100%;
  }

  img {
    flex: 1 0 90%; /* Take up 90% of the container width */
    max-width: 90%; /* Limit width to 90% of the container width */
    margin: 20px 0; /* Adjust spacing for smaller screens */
  }

  .cvButton {
    font-size: 18px;
    padding: 0.8rem 1.5rem;
    width: 100%;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 24px;
    text-align: center;
  }

  .cvButton {
    font-size: 16px;
    padding: 0.6rem 1rem;
  }

  img {
    flex: 1 0 95%; /* Take up 95% of the container width */
    max-width: 95%; /* Limit width to 95% of the container width */
    margin: 20px 0; /* Adjust spacing for smaller screens */
  }
}
